body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 100%;
}

.container {
  padding: 0px;
}

.landing {
  position: relative;
  background: url(/static/media/books2.96d19931.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 750px;
  margin-top: -24px;
  margin-bottom: -50px;
}

.register {
  position: relative;
  background: url(/static/media/books3.58563c89.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 750px;
  margin-top: -24px;
  margin-bottom: -50px;
}

.login {
  position: relative;
  background: url(/static/media/books.eaa24809.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 750px;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}

footer {
  position: relative;
  bottom: 0;
  left: 0;
  width: 100%;
}

.num-books{
  color: #fff;
  font-size: 14px;
  background-color: #bbb;

  width: 25px;
  height: 25px;
  border-radius: 50%;
  padding: 2px 0px 0 8px;
  display: inline-block;
}

.btn-cart {
  min-width: 20px;
}

